@media screen and (max-width: 1000px) {
    body {
        background-color: black;
    }
    .homeImage {
        display: none;
    }
    .mobileNav {        
        margin-bottom: 50px ;     
        position: relative;        
    }
    .articleRow {
        width: 100%;
        display: inline-block;             
    }
    .articleRow div {
        margin-bottom: 15px;  
        width: 100%;      
    }
    .card-body a {
        border: 1px solid white;
    }
    .card {
        min-width: 100%;
    }
}

.navbar-brand {
    background-color: white;
    color: black;     
}
.navbar {
    background-color: white !important; 
    padding-left: 25px !important;
    border-bottom: 2px solid black ;
    color: black;
    font-family: 'Oswald', sans-serif !important;
    font-weight: 400;
}
.nav-item a {
    width: max-content !important;
    color: black !important;
    padding-left: 15px !important;  
    padding-right: 15px !important ;  
    transition: 0.5s;
    margin-right: 5px ;
}
.nav-item a:hover {
    color: white !important; 
    background-color: black;   
}

.homeImage {
    width: 100% !important;    
    background-color: black !important;

}

.homeImage img {
    width: 100% !important;
    max-height: 700px !important;    
}

.homeImage div {
    color: whitesmoke; 
    text-align: left;    
    font-family: 'Oswald', sans-serif !important;
    font-weight: 300;
}
.homeImage div p {
    text-align: center !important;
    margin-top: 15px !important;
    
}

.articleRow {
    background-color: black;
    display: inline-flexbox;
    padding-bottom: 75px;    
}
.articleRow hr {
    background-color: white;
    width: 70%;
    height: 2px;
}

.articleRow h4 {
    display: block;
    width: 100%;
    text-align: center;
    color: white;
    font-family: "Oswald", sans-serif;
    text-transform: uppercase;
    margin-top: 25px ;
}

.articleRow div {
    background-color: black;
    color: white;
}
.articleRow button{ 
    color: white;
    padding-top: 35px ;
    margin:  0 auto;
    font-family: "Oswald",sans-serif;    
}
.articleRow button:hover {
    color: white;
    border-bottom:1px solid white;     
}

.card-body a {
  bottom: 0;
  position: absolute;  
  text-align: center;
  transform: translate(0,50%); 
  margin-right: -50%;
  left: auto;  
  background-color: black;  
  font-family: "Oswald", sans-serif;
  border: none;
  border-radius: 25px;
  transition: 0.5s;
}

.card-body a:hover {
    background-color: white;
    color: black;
}

.card-title {
    font-family: "Oswald", sans-serif;
    font-weight: 650;
    text-transform: uppercase;
}

.card-text {
    font-family: "Oswald", sans-serif;
    font-weight: 300;
}


.hide-me[aria-expanded="true"] {display: none;}

.hide-me2[aria-expanded="false"] {display: none;}
